.book {
    display: flex;
    flex-direction: row;
    text-align: center;
    margin: 3rem;

    &-image {
        max-width: 20rem; 
        height: auto;
        display: block; 
        margin: 1rem 2rem 1rem 2rem;
    }

    &-title,
    &-price {
        margin: 1.2rem 0 1rem 0;
        font-weight: 500;
        font-size: 25px;
    }

    &-button {
        height: 10rem;
        
        &-text {
            margin: 0 auto;
            background-color: #fff023; /* Blue */
            border: none;
            color: rgb(0, 0, 0);
            padding: 15px 32px;
            text-align: center;
            text-decoration: none;
            display: inline-block;
            font-size: 16px;
            margin: 4px 2px;
            cursor: pointer;
            transition-duration: 0.4s;
            border-radius: 10px;

            &:hover {
                background-color: rgb(0, 0, 0); 
                color: rgb(255, 255, 255); 
            }

        }

        
    }

    &-caption {
        margin-left: 2rem; 
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}