$primary-color: #333;       // Define your primary color

.header {
    padding: 20px 0;

    nav {
        height: 5rem;
        display: flex;
        justify-content: space-evenly;
        width: 100%;
        ul {
            width: 100rem;
            display: flex;
            justify-content: center;
            list-style: none;

            li {

                a {
                    text-decoration: none;
                    color: $primary-color;
                    font-weight: 600;
                    font-size: larger;
                    padding: 1rem;

                    &:hover {
                        font-weight: 800;
                        background-color: black;
                        border-radius: 10px;
                        color: white;
                    }
                }
            }
        }
    }
}

.footer {
    margin-top: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}


.tab {
    cursor: pointer;
}